    "code": "import React from 'react';\nimport PropTypes from 'prop-types';\nimport style from 'styled-components';\nimport * as Styles from './style';\nimport ChevronDown from '../Icon/Icons/ChevronDown';\nimport Select from '../Select/Select';\n\nexport const ButtonStyled = style.div`  \n  ${Styles.buttonbase};\n  ${props => Styles[props.size]};\n  ${props => Styles[props.disabled ? 'disabled' : props.type]};\n`;\n\nconst Loading = style.img`\n  width: 24px;\n  padding-left: 10px;\n`;\n\n\n/** All buttons, including text, link and split-buttons, follow the same core principles in dimensions, padding, and font sizes.\n * Combined with simple modifiers, they can be changed in size and appearance.  */\nconst Button = ({\n  disabled,\n  onClick,\n  type,\n  size,\n  label,\n  isSplit,\n  loading,\n  icon,\n  hasIconOnly,\n  isSelect,\n  items,\n  selectPosition,\n  onSelectClick,\n}) => (\n  <ButtonStyled\n    onClick={!disabled ? onClick : undefined}\n    disabled={disabled}\n    size={size}\n    type={type}\n    isSplit={isSplit}\n    icon={icon}\n    hasIconOnly={hasIconOnly}\n    aria-label={label || null}\n  >\n    {label}\n    {isSelect && (type === 'primary' || type === 'secondary') && <Styles.ArrowButton><ChevronDown type={type} size={size} /></Styles.ArrowButton>}\n    {loading && <Loading src=\"./images/loading-gray.svg\" alt=\"loading\" />}\n    {isSplit && (type === 'primary' || type === 'secondary') && <Select onSelectClick={onSelectClick} items={items} type={type} isSplit position={selectPosition} /> }\n  </ButtonStyled>\n);\n\n\nButton.propTypes = {\n  /** Is the button disabled */\n  disabled: PropTypes.bool,\n\n  /** Size of the Button */\n  size: PropTypes.oneOf(['small', 'large', 'medium']),\n\n  /** OnClick handler */\n  onClick: PropTypes.func.isRequired,\n\n  /** Button label */\n  label: PropTypes.string,\n\n  /** Type of button */\n  type: PropTypes.oneOf(['link', 'primary', 'secondary', 'text']),\n\n  /** Is the Button Split  */\n  isSplit: PropTypes.bool,\n\n  /** Is this the Select button with chevron */\n  isSelect: PropTypes.bool,\n\n  /** Is the Button Loading  */\n  loading: PropTypes.bool,\n\n  /** Does the button have only an icon and no label */\n  hasIconOnly: PropTypes.bool,\n\n  /** Icon to show with the label */\n  icon: PropTypes.node,\n\n  /** Items to display in the Split Button popup */\n  items: PropTypes.arrayOf(PropTypes.shape({\n    id: PropTypes.string.isRequired,\n    title: PropTypes.string.isRequired,\n  })),\n\n  /** Position of the Select popup */\n  selectPosition: PropTypes.oneOf(['top', 'bottom']),\n\n  /** Function to call on Split Button selected item click */\n  onSelectClick: PropTypes.func,\n};\n\n\nButton.defaultProps = {\n  disabled: false,\n  isSplit: undefined,\n  loading: false,\n  size: 'medium',\n  type: 'secondary',\n  label: undefined,\n  hasIconOnly: false,\n  icon: undefined,\n  isSelect: undefined,\n  items: undefined,\n  selectPosition: 'bottom',\n  onSelectClick: undefined,\n};\n\n\nexport default Button;\n",
